import React, { useState } from 'react';

const ScheduleComponent: React.FC = () => {
  const [timers, setTimers] = useState([
    { id: 1, timer: 'Timer 1', open: '10.2', time: '10:00', days: [] },
    { id: 2, timer: 'Timer 2', open: '12.0', time: '12:00', days: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'] },
    { id: 3, timer: 'Timer 3', open: '15.0', time: '15:00', days: ['Tue', 'Wed'] },
    { id: 4, timer: 'Timer 4', open: '18.0', time: '18:00', days: ['Fri'] },
    { id: 5, timer: 'Timer 5', open: '21.0', time: '21:00', days: ['Sat'] },
    { id: 6, timer: 'Timer 6', open: '24.0', time: '00:00', days: ['Thu', 'Fri'] },
    { id: 7, timer: 'Timer 7', open: '27.0', time: '03:00', days: ['Fri'] },
    { id: 8, timer: 'Timer 8', open: '30.0', time: '06:00', days: ['Thu'] },
    { id: 9, timer: 'Timer 9', open: '33.0', time: '09:00', days: ['Fri'] },
    { id: 10, timer: 'Timer 10', open: '36.0', time: '12:00', days: [] },
  ]);

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [currentTimer, setCurrentTimer] = useState<any>(null);
  const [modalTab, setModalTab] = useState('basic'); // 'basic' or 'advanced'
  const [modalSize, setModalSize] = useState('default'); // 'default', 'expanded'

  const daysOfWeek = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'];
  
  // Additional settings for the enhanced modal
  const [advancedSettings, setAdvancedSettings] = useState({
    repeat: 'daily',
    endDate: '',
    notifyBefore: 10,
    priority: 'medium',
    sound: 'default',
  });

  // Open modal and set current timer
  const handleEdit = (timer: any) => {
    setCurrentTimer({
      ...timer,
      // Clone to avoid reference issues
      days: [...timer.days]
    });
    setModalTab('basic');
    setModalSize('default');
    setIsModalOpen(true);
  };

  // Close modal
  const closeModal = () => {
    setIsModalOpen(false);
    setCurrentTimer(null);
    setModalTab('basic');
    setModalSize('default');
  };

  // Toggle modal size
  const toggleModalSize = () => {
    setModalSize(modalSize === 'default' ? 'expanded' : 'default');
  };

  // Handle form data changes
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setCurrentTimer((prev: any) => ({ ...prev, [name]: value }));
  };

  // Handle advanced settings changes
  const handleAdvancedChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setAdvancedSettings((prev) => ({ ...prev, [name]: value }));
  };

  // Handle time input changes
  const handleTimeChange = (value: string, type: 'hours' | 'minutes') => {
    const timeComponents = currentTimer.time.split(':');
    let hours = parseInt(timeComponents[0]);
    let minutes = parseInt(timeComponents[1]);
    
    if (type === 'hours') {
      hours = parseInt(value);
    } else {
      minutes = parseInt(value);
    }
    
    // Format hours and minutes to ensure they have proper 24-hour representation
    const formattedHours = hours.toString().padStart(2, '0');
    const formattedMinutes = minutes.toString().padStart(2, '0');
    
    const newTime = `${formattedHours}:${formattedMinutes}`;
    setCurrentTimer((prev: any) => ({ ...prev, time: newTime }));
  };

  // Handle day selection change
  const handleDayChange = (day: string) => {
    setCurrentTimer((prev: any) => {
      const updatedDays = prev.days.includes(day)
        ? prev.days.filter((d: string) => d !== day) // Unselect if already selected
        : [...prev.days, day]; // Add if not selected
      return { ...prev, days: updatedDays };
    });
  };

  // Select all days
  const selectAllDays = () => {
    setCurrentTimer((prev: any) => ({
      ...prev,
      days: [...daysOfWeek]
    }));
  };

  // Clear all days
  const clearAllDays = () => {
    setCurrentTimer((prev: any) => ({
      ...prev,
      days: []
    }));
  };

  // Handle form submission
  const handleSubmit = () => {
    const updatedTimers = timers.map((timer) =>
      timer.id === currentTimer.id ? currentTimer : timer
    );
    setTimers(updatedTimers);
    closeModal();
  };

  // Delete timer
  const handleDelete = () => {
    if (window.confirm(`Are you sure you want to delete ${currentTimer.timer}?`)) {
      const updatedTimers = timers.filter((timer) => timer.id !== currentTimer.id);
      setTimers(updatedTimers);
      closeModal();
    }
  };

  // Calculate modal classes based on size
  const getModalClasses = () => {
    return modalSize === 'expanded' 
      ? "bg-blue-100 border-2 border-blue-800 w-auto max-w-4xl relative shadow-lg"
      : "bg-blue-100 border-2 border-blue-800 w-auto max-w-lg relative shadow-lg";
  };

  return (
    <div className="p-4 bg-gray-50 min-h-screen">
      <h1 className="text-xl font-bold mb-4 text-gray-900">Schedule Timers</h1>
      <div className="overflow-hidden rounded-lg shadow-sm bg-white">
        <div className="overflow-x-auto">
          <table className="min-w-full">
            <thead className="bg-gray-100">
              <tr>
                <th className="py-3 px-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Timer
                </th>
                <th className="py-3 px-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Open (%)
                </th>
                <th className="py-3 px-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Time
                </th>
                {daysOfWeek.map((day, index) => (
                  <th key={index} className="py-3 px-4 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider">
                    {day}
                  </th>
                ))}
                <th className="py-3 px-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-100">
              {timers.map((timer) => (
                <tr key={timer.id} className="hover:bg-gray-50 transition-colors">
                  <td className="py-3 px-4 text-sm text-gray-700 font-medium">{timer.timer}</td>
                  <td className="py-3 px-4 text-sm text-gray-700">{timer.open}%</td>
                  <td className="py-3 px-4 text-sm text-gray-700">{timer.time}</td>
                  {daysOfWeek.map((day, index) => (
                    <td key={index} className="py-3 px-4 text-center">
                      <div
                        className={`w-6 h-6 mx-auto border-2 flex items-center justify-center ${
                          timer.days.includes(day)
                            ? 'border-blue-500 bg-blue-50 text-blue-700'
                            : 'border-gray-200 bg-gray-50 text-gray-400'
                        } rounded-md transition-all hover:scale-105 cursor-pointer`}
                      >
                        {timer.days.includes(day) && (
                          <span className="text-xs">✔️</span>
                        )}
                      </div>
                    </td>
                  ))}
                  <td className="py-3 px-4 text-sm">
                    <button
                      onClick={() => handleEdit(timer)}
                      className="bg-blue-500 text-white py-1 px-3 rounded-md hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-colors"
                    >
                      Edit
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* Enhanced Modal */}
      {isModalOpen && currentTimer && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <div className={getModalClasses()}>
            {/* Header with title, resize, and close button */}
            <div className="flex justify-between items-center bg-blue-500 px-3 py-2 border-b-2 border-blue-800">
              <h2 className="font-bold text-white">{currentTimer.timer}</h2>
              <div className="flex items-center space-x-2">
                <button 
                  onClick={toggleModalSize}
                  className="bg-gray-600 text-white font-bold px-2 w-8 h-8 flex items-center justify-center hover:bg-gray-700 transition-colors"
                  title={modalSize === 'default' ? 'Expand modal' : 'Shrink modal'}
                >
                  {modalSize === 'default' ? '⤢' : '⤡'}
                </button>
                <button 
                  onClick={closeModal}
                  className="bg-black text-white font-bold px-2 w-8 h-8 flex items-center justify-center hover:bg-gray-800 transition-colors"
                >
                  X
                </button>
              </div>
            </div>
            
            {/* Tabs */}
            <div className="flex border-b border-blue-300">
              <button
                className={`px-4 py-2 font-medium ${
                  modalTab === 'basic' 
                    ? 'bg-blue-500 text-white' 
                    : 'bg-blue-200 text-blue-800 hover:bg-blue-300'
                }`}
                onClick={() => setModalTab('basic')}
              >
                Basic Settings
              </button>
              <button
                className={`px-4 py-2 font-medium ${
                  modalTab === 'advanced' 
                    ? 'bg-blue-500 text-white' 
                    : 'bg-blue-200 text-blue-800 hover:bg-blue-300'
                }`}
                onClick={() => setModalTab('advanced')}
              >
                Advanced Settings
              </button>
            </div>
            
            {/* Modal content with improved spacing */}
            <div className="p-8">
              {modalTab === 'basic' && (
                <>
                  <div className="mb-5 flex items-center">
                    <label className="block w-32 font-medium text-gray-800">Name</label>
                    <input
                      type="text"
                      name="timer"
                      value={currentTimer.timer}
                      onChange={handleChange}
                      className="border-2 border-gray-400 px-2 py-1 w-full max-w-xs focus:border-blue-500 focus:ring-1 focus:ring-blue-500 outline-none"
                    />
                  </div>
                  
                  <div className="mb-5 flex items-center">
                    <label className="block w-32 font-medium text-gray-800">Percent open</label>
                    <input
                      type="number"
                      step="0.1"
                      name="open"
                      value={currentTimer.open}
                      onChange={handleChange}
                      className="border-2 border-gray-400 px-2 py-1 w-32 focus:border-blue-500 focus:ring-1 focus:ring-blue-500 outline-none"
                    />
                    <span className="ml-2 text-gray-800">%</span>
                  </div>
                  
                  <div className="mb-5 flex items-center">
                    <label className="block w-32 font-medium text-gray-800">Time</label>
                    <div className="flex items-center">
                      <input
                        type="number"
                        min="0"
                        max="23"
                        value={currentTimer.time.split(':')[0]}
                        onChange={(e) => handleTimeChange(e.target.value, 'hours')}
                        className="border-2 border-gray-400 px-2 py-1 w-16 focus:border-blue-500 focus:ring-1 focus:ring-blue-500 outline-none"
                      />
                      <span className="mx-1 text-gray-800 font-bold">:</span>
                      <input
                        type="number"
                        min="0"
                        max="59"
                        value={currentTimer.time.split(':')[1]}
                        onChange={(e) => handleTimeChange(e.target.value, 'minutes')}
                        className="border-2 border-gray-400 px-2 py-1 w-16 focus:border-blue-500 focus:ring-1 focus:ring-blue-500 outline-none"
                      />
                    </div>
                  </div>
                  
                  <div className="mb-6">
                    <div className="flex justify-between items-center mb-2">
                      <label className="block font-medium text-gray-800">Day of the week</label>
                      <div className="space-x-2">
                        <button
                          type="button"
                          onClick={selectAllDays}
                          className="bg-blue-600 text-white text-xs px-2 py-1 rounded hover:bg-blue-700"
                        >
                          Select All
                        </button>
                        <button
                          type="button"
                          onClick={clearAllDays}
                          className="bg-gray-600 text-white text-xs px-2 py-1 rounded hover:bg-gray-700"
                        >
                          Clear All
                        </button>
                      </div>
                    </div>
                    <div className="grid grid-cols-7 gap-2 w-full">
                      {daysOfWeek.map((day) => (
                        <button
                          key={day}
                          type="button"
                          onClick={() => handleDayChange(day)}
                          className={`py-2 border-2 transition-colors ${
                            currentTimer.days.includes(day)
                              ? 'bg-blue-500 text-white border-blue-700 hover:bg-blue-600'
                              : 'bg-gray-200 text-gray-700 border-gray-500 hover:bg-gray-300'
                          }`}
                        >
                          {day}
                        </button>
                      ))}
                    </div>
                  </div>
                </>
              )}

              {modalTab === 'advanced' && (
                <>
                  <div className="mb-5 flex items-center">
                    <label className="block w-32 font-medium text-gray-800">Repeat</label>
                    <select
                      name="repeat"
                      value={advancedSettings.repeat}
                      onChange={handleAdvancedChange}
                      className="border-2 border-gray-400 px-2 py-1 w-full max-w-xs focus:border-blue-500 focus:ring-1 focus:ring-blue-500 outline-none"
                    >
                      <option value="daily">Daily</option>
                      <option value="weekly">Weekly</option>
                      <option value="monthly">Monthly</option>
                      <option value="custom">Custom</option>
                    </select>
                  </div>
                  
                  <div className="mb-5 flex items-center">
                    <label className="block w-32 font-medium text-gray-800">End Date</label>
                    <input
                      type="date"
                      name="endDate"
                      value={advancedSettings.endDate}
                      onChange={handleAdvancedChange}
                      className="border-2 border-gray-400 px-2 py-1 w-full max-w-xs focus:border-blue-500 focus:ring-1 focus:ring-blue-500 outline-none"
                    />
                  </div>
                  
                  <div className="mb-5 flex items-center">
                    <label className="block w-32 font-medium text-gray-800">Notify Before</label>
                    <input
                      type="number"
                      name="notifyBefore"
                      value={advancedSettings.notifyBefore}
                      onChange={handleAdvancedChange}
                      className="border-2 border-gray-400 px-2 py-1 w-20 focus:border-blue-500 focus:ring-1 focus:ring-blue-500 outline-none"
                    />
                    <span className="ml-2 text-gray-800">minutes</span>
                  </div>
                  
                  <div className="mb-5 flex items-center">
                    <label className="block w-32 font-medium text-gray-800">Priority</label>
                    <select
                      name="priority"
                      value={advancedSettings.priority}
                      onChange={handleAdvancedChange}
                      className="border-2 border-gray-400 px-2 py-1 w-full max-w-xs focus:border-blue-500 focus:ring-1 focus:ring-blue-500 outline-none"
                    >
                      <option value="low">Low</option>
                      <option value="medium">Medium</option>
                      <option value="high">High</option>
                      <option value="critical">Critical</option>
                    </select>
                  </div>
                  
                  <div className="mb-5 flex items-center">
                    <label className="block w-32 font-medium text-gray-800">Sound</label>
                    <select
                      name="sound"
                      value={advancedSettings.sound}
                      onChange={handleAdvancedChange}
                      className="border-2 border-gray-400 px-2 py-1 w-full max-w-xs focus:border-blue-500 focus:ring-1 focus:ring-blue-500 outline-none"
                    >
                      <option value="default">Default</option>
                      <option value="bell">Bell</option>
                      <option value="chime">Chime</option>
                      <option value="alert">Alert</option>
                      <option value="silent">Silent</option>
                    </select>
                  </div>
                </>
              )}
              
              <div className="flex justify-between mt-8">
                <button
                  type="button"
                  onClick={handleDelete}
                  className="bg-red-500 text-white py-2 px-6 font-bold border-2 border-red-700 hover:bg-red-600 transition-colors shadow-md"
                >
                  Delete
                </button>
                <div className="space-x-4">
                  <button
                    type="button"
                    onClick={closeModal}
                    className="bg-gray-400 text-white py-2 px-6 font-bold border-2 border-gray-600 hover:bg-gray-500 transition-colors shadow-md"
                  >
                    Cancel
                  </button>
                  <button
                    type="button"
                    onClick={handleSubmit}
                    className="bg-green-500 text-white py-2 px-10 font-bold border-2 border-green-700 hover:bg-green-600 transition-colors shadow-md"
                  >
                    Save
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ScheduleComponent;