// components/gate_condition.tsx
'use client'

const GateInfo: React.FC = () => {
  const gateData = [
    { label: 'Refresh Time', value: '12:34 PM', icon: '⏱️' },
    { label: 'Status', value: 'Active', icon: '✅' },
    { label: 'Condition', value: 'Good', icon: '⚙️' },
    { label: 'Open/Close', value: 'Closed', icon: '🚪' },
    { label: 'Temperature', value: '35°C', icon: '🌡️' },
    { label: 'Voltage', value: '220V', icon: '🔋' },
  ]

  return (
    <div className="w-full bg-white rounded-lg shadow-md p-6 border border-gray-200">
      <h3 className="text-xl font-semibold text-gray-800 mb-6">Gate Information</h3>
      <div className="space-y-4">
        {gateData.map((item, index) => (
          <div key={index} className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <span className="text-xl">{item.icon}</span>
              <span className="text-sm text-gray-700">{item.label}</span>
            </div>
            <span className="text-sm font-semibold text-gray-900">{item.value}</span>
          </div>
        ))}
      </div>
    </div>
  )
}

export default GateInfo